<template>
  <div id="app">
    <pay-info v-if="ispay"></pay-info>
    <!-- <action-clock></action-clock> -->
  </div>
</template>

<script>

// import payInfo from '@/pages/payInfo/App'
import actionClock from '@/pages/actionClock/actionClock'
export default {
  name: 'App',
  data () {
    return {
      ispay:false,
      isclock:true,
      userInfo:'',//个人信息
    }
  },
  components: {
    // payInfo,
    actionClock,
  },
  created(){
    this.GetUserInfo(this);
  },
  methods:{
     GetUserInfo(self){
        self.$toast.loading({
            duration:0,
            forbidClick:true,
            loadingType:'circular',
            message:'信息拉取中...'
        })
        WebApp.Init('wxc5216d15dd321ac5',//appid
        function(result,data){//result:请求状态,data 请求结果
            console.log(Options.GetUserInfo());
            // self.tabbar = common.tabbar;
            self.userInfo = Options.GetUserInfo();
            // 开启测试服务器
            Options.TestServer = false;
            self.$toast.clear();
            // 新用户注册信息
            self.Register(self);
        });
    },
    // 信息注册
    Register(self){
        self.$toast.loading({
          duration:0,
          forbidClick:true,
          loadingType:'circular',
          message:'信息注册中...'
        })
        TD_Request('us','enter',{
            uid:self.userInfo.openid,
            nickname:self.userInfo.nickname,
            headicon:self.userInfo.headimgurl
        },function(code,data){
            console.log(data)
            self.$toast.clear();
            self.Orders(self,self.userInfo.openid)
        },function(code,data){
            console.log(data)
            self.$toast.clear()
        })
    },
    // 订单信息
    
  }
}
</script>

<style scoped lang="less">

</style>



